<template>
  <div>
    <page-title/>

    <div class="panel">
      <h3>عمومی</h3>

      <div class="mt-2">
        <label class="form-label">عنوان وبسایت</label>
        <input type="text" class="form-control"
               v-model="model.general_meta_title"
               placeholder="title tag"
        />
      </div>

      <div class="mt-2">
        <label class="form-label">توضیحات وبسایت</label>
        <textarea class="form-control"
                  v-model="model.general_meta_description"
                  placeholder="meta description"
        ></textarea>
      </div>
    </div>

    <div class="panel mt-3">
      <h3>
        بخش اول سایت (پروفایل دکتر)
      </h3>

      <div class="row mt-4">
        <div class="col-md">
          <div class="mt-2">
            <label class="form-label">تصویر پروفایل</label>
            <input type="file" ref="profileInput" class="d-none"
                   @change="onProfileInputChange"
                   accept="image/jpeg,image/png,image/gif"
            />

            <div class="d-flex flex-column justify-content-center align-items-center">
              <div class="overflow-hidden bg-secondary" style="width: 290px; border: 5px solid white;"
                   :class="{'rounded-circle' : !profileCropper}"
              >
                <div class="aspect-100 cursor-pointer" @click="!profileCropper ? $refs.profileInput.click() : null">
                  <img src="" ref="profileImg"/>
                </div>
              </div>

              <div v-if="profileCropper">
                <button type="button" class="btn btn-primary btn-sm" @click="profileImageCropperAction(true)">
                  تایید
                </button>

                <button type="button" class="btn btn-warning btn-sm" @click="profileImageCropperAction(false)">
                  انصراف
                </button>
              </div>
            </div>
          </div>
        </div>

        <div class="col-md">
          <div class="mt-2">
            <label class="form-label">عنوان اول</label>
            <input type="text" class="form-control" v-model="model.general_profile_first_title"/>
          </div>

          <div class="mt-2">
            <label class="form-label">عنوان دوم</label>
            <input type="text" class="form-control" v-model="model.general_profile_second_title"/>
          </div>

          <div class="mt-2">
            <label class="form-label">عنوان سوم</label>
            <input type="text" class="form-control" v-model="model.general_profile_third_title"/>
          </div>
        </div>
      </div>
    </div>

    <div class="panel mt-3">
      <h3>خدمات قابل ارائه</h3>

      <div class="mt-4 row">

        <div class="col-md">
          <form @submit.prevent="onServiceFormPost">
            <div class="">
              <label class="form-label">عنوان</label>
              <input type="text" class="form-control" v-model="serviceModel.title" required/>
            </div>

            <div class="mt-2">
              <label class="form-label">توضیحات</label>
              <textarea class="form-control" v-model="serviceModel.description" required></textarea>
            </div>

            <div class="mt-2">
              <label class="form-label">آیکون</label>
              <input type="file" required ref="serviceIconInput" accept="image/jpeg,image/png" class="form-control">
            </div>

            <div class="mt-3 d-flex flex-row-reverse">
              <button type="submit" class="btn btn-primary">افزودن</button>
              <button type="reset" class="btn btn-warning me-2">جدید</button>
            </div>
          </form>
        </div>

        <div class="col-md">
          <div class="alert alert-light shadow-sm mb-2 row" v-for="(x,k) in this.model.services" :key="k">

            <div class="col-sm-4">
              <div class="aspect-100 rounded-circle">
                <img :src="x.icon" :alt="x.title"/>
              </div>
            </div>
            <div class="col-sm d-flex flex-column">
              <h5 class="fw-bolder">{{ x.title }}</h5>
              <div class="text-muted">{{ x.description }}</div>

              <div class="mt-auto pt-2 text-center">
                <span class="fa fa-trash cursor-pointer text-danger"
                      @click="this.model.services.splice(k,1)"
                ></span>
              </div>
            </div>

          </div>
        </div>

      </div>
    </div>

    <div class="panel mt-3">
      <h3>معرفی</h3>

      <div class="row mt-4">
        <div class="col-md">
          <label class="form-label">
            تصویر
          </label>
          <input type="file" ref="introductionImageInput" @change="onIntroductionImageChanged"/>
          <img :src="model.introduction_image" class="mt-2"/>
        </div>

        <div class="col-md">

          <div>
            <label class="form-label">عنوان اول</label>
            <input type="text" class="form-control" v-model="model.introduction_first_title"/>
          </div>

          <div class="mt-2">
            <label class="form-label">توضیح مختصر اول</label>
            <input type="text" class="form-control" v-model="model.introduction_first_description"/>
          </div>

          <div class="mt-2">
            <label class="form-label">عنوان دوم</label>
            <input type="text" class="form-control" v-model="model.introduction_second_title"/>
          </div>

          <div class="mt-2">
            <label class="form-label">توضیح دوم</label>
            <textarea class="form-control" v-model="model.introduction_second_description"></textarea>
          </div>

          <div class="mt-2">
            <label class="form-label">لیست (مدارک و افتخارات)</label>
            <form class="input-group" @submit.prevent="onIntroductionListSubmit">
              <input type="text" class="form-control" v-model="introductionListItem"/>
              <button type="submit" class="btn btn-primary">اضافه کردن</button>
            </form>

            <ul class="mt-2">
              <li v-for="(x,k) in model.introduction_list" :key="k" class="d-flex align-items-center">
                {{ x }}

                <div class="ms-auto ps-1">
                  <span class="fa fa-trash text-danger cursor-pointer" @click="model.introduction_list.splice(k,1)"
                  ></span>
                </div>
              </li>
            </ul>
          </div>

        </div>
      </div>
    </div>

    <div class="panel mt-3">
      <h3>اطلاعات تماس</h3>

      <div class="mt-4 row">

        <div class="col-md">
          <div>
            <label class="form-label">اسکریپت نقشه (embed)</label>
            <textarea class="form-control" v-model="model.contact_map_embed_script"></textarea>
          </div>

          <div class="mt-2">
            <label class="form-label">تصویر</label>
            <input type="file" class="form-control" @change="onContactImageInputChange"/>
            <img :src="model.contact_image" class="mt-2"/>
          </div>
        </div>

        <div class="col-md">
          <div>
            <label class="form-label">آدرس</label>
            <textarea class="form-control" v-model="model.contact_address"></textarea>
          </div>

          <div class="mt-2">
            <label class="form-label">شماره(های) تماس</label>

            <form @submit.prevent="onContactPhonesFormSubmit" class="input-group">
              <select class="form-select" required v-model="contactPhoneModel.type">
                <option>phone</option>
                <option>mobile</option>
              </select>
              <input type="tel" required pattern="^[0-9]+$" class="form-control" v-model="contactPhoneModel.number"/>

              <button class="btn btn-primary" type="submit">
                اضافه کردن
              </button>
            </form>

            <ul class="mt-2">
              <li v-for="(x,k) in model.contact_phones" :key="k" class="d-flex align-items-center">
                <span v-if="x.type === 'mobile'" class="fa fa-mobile-alt fa-fw"></span>
                <span v-if="x.type === 'phone'" class="fa fa-phone fa-fw"></span>
                <span class="ms-1">{{ x.number }}</span>

                <div class="ms-auto ps-1">
                  <span class="fa fa-trash text-danger" @click="model.contact_phones.splice(k,1)"></span>
                </div>
              </li>
            </ul>
          </div>

          <div class="mt-2">
            <label class="form-label">شبکه‌های اجتماعی</label>

            <form @submit.prevent="onContactSocialsFormSubmit" class="input-group">
              <select class="form-select" required v-model="contactSocialModel.social">
                <option :value="undefined">-- انتخاب شبکه اجتماعی --</option>
                <option>telegram</option>
                <option>instagram</option>
                <option>facebook</option>
                <option>twitter</option>
                <option>linkedin</option>
                <option>youtube</option>
              </select>
              <input type="url" required class="form-control" v-model="contactSocialModel.url"/>

              <button class="btn btn-primary" type="submit">
                اضافه کردن
              </button>
            </form>

            <ul class="mt-2">
              <li v-for="(x,k) in model.contact_socials" :key="k" class="d-flex align-items-center">
                <span class="">{{ x.social }}</span>
                <span class="ms-1">{{ x.url }}</span>

                <div class="ms-auto ps-1">
                  <span class="fa fa-trash text-danger" @click="model.contact_socials.splice(k,1)"></span>
                </div>
              </li>
            </ul>
          </div>
        </div>

      </div>
    </div>


    <div class="panel mt-3 text-center" id="footer-control-actions">
      <button type="button" class="btn btn-success" v-if="working.length <= 0" @click="saveChanges">
        ذخیره تغییرات
      </button>
      <div class="d-inline-block btn btn-success" v-if="working.length > 0">
        <span class="fa fa-spin fa-sync"></span>
      </div>
    </div>
  </div>
</template>

<script>
import PageTitle from '../../tools/PageTitle'
import 'cropperjs/dist/cropper.min.css'
import Cropper from 'cropperjs'
import { dispatchNotification } from '../../library/stores/notifications'

export default {
  name: 'WebsiteSettings',
  components: { PageTitle },
  data: () => ({
    working: [],

    model: {
      introduction_image: '',
      introduction_list: [],
      services: [],
      contact_phones: [],
      contact_socials: [],
    },
    serviceModel: {
      icon: null,
      title: '',
      description: ''
    },
    introductionListItem: '',
    contactPhoneModel: {
      type: 'phone',
      number: ''
    },
    contactSocialModel: {
      social: undefined,
      url: ''
    },
    profileCropper: undefined
  }),
  methods: {
    onProfileInputChange() {
      if (this.$refs.profileInput.files.length > 0) {
        const fd = new FileReader()
        fd.onload = () => {
          this.$refs.profileImg.src = fd.result
          this.profileCropper = new Cropper(this.$refs.profileImg, { aspectRatio: 1 })
        }
        fd.readAsDataURL(this.$refs.profileInput.files[0])
      }
    },
    profileImageCropperAction(accept = true) {
      if (accept) {
        this.model.general_profile_image = this.profileCropper.getCroppedCanvas()
            .toDataURL()
      }

      this.profileCropper.destroy()
      this.profileCropper = undefined
      this.$refs.profileImg.src = this.model.general_profile_image
    },
    onServiceFormPost() {
      if (this.$refs.serviceIconInput.files.length > 0) {
        const fd = new FileReader()
        fd.onload = () => {
          this.serviceModel.icon = fd.result
          this.model.services.push({ ...this.serviceModel })
          this.serviceModel = {}
          this.$refs.serviceIconInput.value = ''
        }
        fd.readAsDataURL(this.$refs.serviceIconInput.files[0])
      }
    },
    onIntroductionImageChanged() {
      if (this.$refs.introductionImageInput.files.length > 0) {
        const fd = new FileReader()
        fd.onload = () => this.model.introduction_image = fd.result
        fd.readAsDataURL(this.$refs.introductionImageInput.files[0])
      }
    },
    onIntroductionListSubmit() {
      if (this.introductionListItem.trim()) {
        this.model.introduction_list.push(this.introductionListItem.trim())
      }

      this.introductionListItem = ''
    },
    onContactImageInputChange(e) {
      if (e.target.files.length > 0) {
        const fd = new FileReader()
        fd.onload = () => this.model.contact_image = fd.result
        fd.readAsDataURL(e.target.files[0])
      }
    },
    onContactPhonesFormSubmit() {
      this.model.contact_phones.push({ ...this.contactPhoneModel })
      this.contactPhoneModel.number = ''
    },
    onContactSocialsFormSubmit() {
      if (!this.contactSocialModel.social || !this.contactSocialModel.social.trim() || !this.contactSocialModel.url.trim()) {
        return
      }
      this.model.contact_socials.push({ ...this.contactSocialModel })
      this.contactSocialModel.url = ''
    },

    retrieveSettings() {
      this.working.push('retrieve')
      this.$fetch('/options/all')
          .then(x => {
            if (x.data && typeof x.data === 'object') {
              this.model = x.data
            }
            this.$refs.profileImg.src = x.data.general_profile_image
          })
          .finally(() => this.working.pop())
    },
    saveChanges() {
      if (this.working.length > 0) {
        return
      }

      this.working.push('save item')
      this.$fetch('/options/all', 'post', this.model)
          .then(() => dispatchNotification('اطلاعات با موفقیت ثبت شد'))
          .finally(() => this.working.pop())
    }
  },
  mounted() {
    this.retrieveSettings()
  }
}
</script>

<style scoped>
#footer-control-actions {
  position: sticky;
  bottom: 5px;
  z-index: 9999;
  border: thin solid var(--bs-info);
  border-radius: 5px;
}
</style>

<style>
.cropper-face {
  border-radius: 50%;
}
</style>
